import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper';

export default {
  name: 'SwiperCarousel',
  components: {
    Swiper,
    SwiperSlide
  },
  directives: {
    swiper: directive
  },
  props: ['type'],
  data() {
    return {
      swiperOptions: {
        watchOverflow: true,
        centeredSlidesBounds: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        spaceBetween: 10,
        breakpoints: {
          320: {
            slidesPerView: 2
          },
          560: {
            slidesPerView: 3
          },
          768: {
            slidesPerView: 4
          },
          992: {
            slidesPerView: 5
          },
          1248: {
            slidesPerView: 6
          },
          1440: {
            slidesPerView: 7
          }
        }
      },
      charactersSwiperOptions: {
        watchOverflow: true,
        centeredSlidesBounds: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        slidesPerView: 2,
        spaceBetween: 10
      }
    };
  }
};

